.rtl( @rule, @value ) {
    [dir="rtl"] & {
        @{rule}: @value
    }
}

.rtl-root ( @rule, @value ) {
    [dir="rtl"]& {
        @{rule}: @value
    }
}

.text-align( @textAlign ) {
    text-align: @textAlign;
}

.text-align( @textAlign ) when ( @textAlign = left ) {
    .rtl( text-align, right )
}

.text-align( @textAlign ) when ( @textAlign = right ) {
    .rtl( text-align, left )
}

.text-align( @textAlign ) when ( @textAlign = center ), ( @textAlign = inherit ) {
    .rtl( text-align, @textAlign )
}

.text-align-root( @textAlign ) {
    text-align: @textAlign;
}

.text-align-root( @textAlign ) when ( @textAlign = left ) {
    .rtl-root( text-align, right )
}

.text-align-root( @textAlign ) when ( @textAlign = right ) {
    .rtl-root( text-align, left )
}

.text-align-root( @textAlign ) when ( @textAlign = center ), ( @textAlign = inherit ) {
    .rtl-root( text-align, @textAlign )
}

.margin ( @m1, @m2, @m3, @m4 ) {
    margin: @m1  @m2  @m3  @m4 ;
    .rtl ( margin, @m1  @m4  @m3  @m2 )
}

.padding ( @p1, @p2, @p3, @p4 ) {
    padding: @p1 @p2 @p3 @p4;
    .rtl( padding, @p1 @p4 @p3 @p2 )
}

.border-width ( @b1, @b2, @b3, @b4 ) {
    border-width: @b1 @b2 @b3 @b4;
    .rtl( border-width, @b1 @b4 @b3 @b2 )
}

.margin-root ( @m1, @m2, @m3, @m4 ) {
    margin: @m1  @m2  @m3  @m4 ;
    .rtl-root ( margin, @m1  @m4  @m3  @m2 )
}

.padding-root ( @p1, @p2, @p3, @p4 ) {
    padding: @p1 @p2 @p3 @p4;
    .rtl-root ( padding, @p1 @p4 @p3 @p2 )
}

.border-width-root ( @b1, @b2, @b3, @b4 ) {
    border-width: @b1 @b2 @b3 @b4;
    .rtl-root ( border-width, @b1 @b4 @b3 @b2 )
}

.left ( @left: 0 ) {
    left: @left;
    right: auto;

    .rtl ( right, @left );
    .rtl ( left, auto );
}

.right ( @right: 0 ) {
    right: @right;
    left: auto;

    .rtl ( left, @right );
    .rtl ( right, auto );
}

.left-right ( @left, @right ) {
    left: @left;
    right: @right;

    .rtl ( left, @right );
    .rtl ( right, @left );
}

.left-root ( @left: 0 ) {
    left: @left;
    right: auto;

    .rtl-root ( right, @left );
    .rtl-root ( left, auto );
}

.right-root ( @right: 0 ) {
    right: @right;
    left: auto;

    .rtl-root ( left, @right );
    .rtl-root ( right, auto );
}

.left-right-root ( @left, @right ) {
    left: @left;
    right: @right;

    .rtl-root ( left, @right );
    .rtl-root ( right, @left );
}

.float( @float ) {
    float: @float;
}

.float( @float ) when ( @float = left ) {
    .rtl( float, right )
}

.float( @float ) when ( @float = right ) {
    .rtl( float, left )
}

.absolute-center {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.overflow-scrolling ( @value: touch ) {
    -webkit-overflow-scrolling: @value;
    -moz-overflow-scrolling: @value;
    overflow-scrolling: @value;
}

.rtlFlip() {
    .rtl( transform, scaleX( -1 ) );
}
