// Begin KOS Styles

.bgprogram
{
    margin: 1.4em 0;
    padding: @grid*5;
    border: 1px solid;
    border-color: @colorBorderCode;
    background: @colorBgCode;
    border-radius: 3px;
}

.attentionbodykos-wrapped,
.attentioncontinue1kos {
  border: 1px solid @colorBorderCode;
  border-left: 3px solid @colorAttentionLine;
  margin-top: 2.3em;
  margin-bottom: 2.3em;
  padding-left: @grid*5;
  padding-top: @grid/2;
  padding-bottom: @grid/2;
}

.attentioncontinue1kos {
  padding-top: 1em;
  padding-bottom: 1em;
}

.codeheadingkos
{
    padding: 4px 8px;
    margin: 1.4em 0 -1.4em;
    font-family: @fontFamilyMono;
    color: @colorCodeHeading;
    border-left: 1px solid;
    border-right: 1px solid;
    border-top: 1px solid;
    border-color: @colorBorderCode;
    background: @colorBgCodeHeading;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}

.codeheadingkos + .bgprogram {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.programcharacterkos {
    font-family: @fontFamilyMono !important;
    padding: 0px @grid/2;
    margin: -1px;
    background: @colorBgCharacterCode;
    border: 1px solid;
    border-color: @colorBgCharacterCodeBorder;
    border-radius: 3px;
    font-style: normal !important;
    .ie7 & {
        *display: inline; zoom: 1;
    }
}

.programkos,
.programckos,
.programmakefilekos,
.programedlkos,
.programcdlkos,
.programidlkos,
.programcfgkos,
.programyamlkos {
    white-space: pre-wrap;
    word-wrap: initial;
    overflow: auto;
    font-family: @fontFamilyMono;
}

.programkos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programckos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programmakefilekos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programedlkos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programcdlkos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programidlkos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programcfgkos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };
.programyamlkos { .margin( 0, 0, 0, @generalGridSpacing*0 ) };

// End KOS Styles