.icon() {
    .icon-base(auto, auto);
}

.icon(@width) {
    .icon-base(@width, @width);
}

.icon(@width, @height) {
    .icon-base(@width, @height);
}

.icon(@width, @posx, @posy) {
    @pos: " -@{posx} -@{posy}";
    .icon-base(@width, @width, @pos);
}

.icon(@width, @height, @posx, @posy) {
    @pos: " -@{posx} -@{posy}";
    .icon-base(@width, @height, @pos);
}

.icon-single(@width, @height, @sprite) {
    .icon-base(@width, @height, "", @sprite, "../../images/");
}

.icon-base(@width; @height; @pos: ""; @sprite: "help_ui"; @bgUrlPreffix: "") {
    @ext: "png";
    @size: image-width("../../images/@{sprite}.@{ext}");

    display: inline-block;
    width: @width;
    height: @height;
    background-image: ~"url(@{bgUrlPreffix}@{sprite}.@{ext})";
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: @pos;
    background-size: @size;

    @media @hidpi {
        background-image: ~"url(@{bgUrlPreffix}@{sprite}@2x.@{ext})"
    }
}