@import (reference) "../styles";

@fontFamilyMono: Consolas, Courier New, monospace;
//@lineHeight: 1.5;

@mobilePaddings: @grid*5;
@generalGridSpacing: @grid*5;

@mobileHeaderHeight: 56px;

*, *:before, *:after {
    box-sizing: inherit;

    @media screen and (-webkit-min-device-pixel-ratio: 0) {
        max-height: 999999px;
    }

    .ie7 & {
        box-sizing: content-box;
        content: none
    }
}

html {
    margin: 0;
    padding: 0;
    font: @font;
    box-sizing: border-box;
    color: @textColorNormal;
    height: 100%;
    width: 100%;
    background-color: @bgColorNormal;
    overflow: hidden;

    @media @bpLessMedium {
        color: @textColorAccented;
        &.is-opera-mini {
            height: auto;
            overflow: auto;
        }
    }

    &[lang="el-GR"],
    &[lang="vi-VN"] {
        font-family: @fontFamilyGreece;
    }
}

body#who-content {
    height: 100%;

    margin: 0 auto;
    padding: 0;

    font: inherit;

    .ie7 & {
        font: @font;

        [lang="el-GR"] &,
        [lang="vi-VN"] & {
            font-family: @fontFamilyGreece;
        }
    }

    @media @bpLessMedium {
        //overflow-y: auto;

        .is-opera-mini & {
            height: auto;
        }
    }
}

a {
    color: @textColorLink;
    text-decoration: underline;

    &:hover {
        text-decoration: none;
    }
}


h1 {
    font-size: 1rem;
    font-weight: 700;

    .ie7 &, .ie8 & {
        font-size: 16px;
    }
}

input[type="text"],
input[type="password"],
input[type="search"],
input[type="email"] {
    border: 1px solid @borderColor;
    border-radius: 0;
    padding: @grid*2 @grid*3;
    appearance: none;

    &:focus {
        outline: none;
    }
}

input[type="search"]::-webkit-search-cancel-button {
    display: none;
}

a,
button {
    -webkit-tap-highlight-color: fade( black, 0% );

    &:active,
    &:focus {
        outline: none;
    }

    @media @bpLessMedium {
        .is-responsive &:active {
            opacity: .8;
        }
    }
}

a, button, input {
    pointer-events: initial;
}

img {
    border: none;
    vertical-align: text-bottom;
}

mark {
    background: @markColor;
    color: @textColorAccented;
    padding: 0 @grid/2;
    margin: 0 -@grid/2;
    border-radius: 3px;

    a & {
        color: @textColorLink;
    }
}

.g-hidden {
    display: none !important;
}

.desktop-only {
    @media @bpLessMedium {
        .is-responsive & {
            display: none;
        }
    }
}

.mobile-only {
    display: none;

    @media @bpLessMedium {
        .is-responsive & {
            display: block;
        }
    }
}

.g-scrollable {
    .overflow-scrolling();
}

